import React, { useState, useEffect } from "react";
import { Link, useNavigate  } from "react-router-dom";
import { get, query, orderByChild, equalTo, ref as databaseRef, push } from "firebase/database";
import { database } from "../firebase";

const Home = () => {
  const [nome, setNome] = useState("");
  const [usuarioCadastrado, setUsuarioCadastrado] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    // Verifique se o nome do usurio j foi cadastrado no localStorage
    const nomeLocalStorage = localStorage.getItem("nomeUsuario");
    if (nomeLocalStorage) {
      setUsuarioCadastrado(true);
      setNome(nomeLocalStorage);
    }
  }, []);

  const handleCadastroUsuario = () => {
    const usuariosRef = databaseRef(database, 'usuarios');

    const usuarioQuery = query(usuariosRef, orderByChild('nome'), equalTo(nome));

    get(usuarioQuery).then((snapshot) => {
      if (snapshot.exists()) {
        // Se o usurio j existe, no faa nada
        alert('Este usurio j est cadastrado.');
      } else {
        // Se o usurio no existe, adicione um novo usurio  lista
        const novaPontuacao = 0;
        const novoUsuario = {
          nome: nome,
          pontuacao: novaPontuacao
        };

        // Adicione o novo usurio  lista no Firebase
        push(usuariosRef, novoUsuario)
          .then(() => {
            alert('Usurio cadastrado com sucesso!');
            // Salve o nome do usurio no localStorage
            localStorage.setItem("nomeUsuario", nome);
            setUsuarioCadastrado(true);
            // Redirecione para a prxima pgina aps o cadastro
            navigate("/about");
          })
          .catch((error) => {
            console.error('Erro ao cadastrar usurio: ', error);
          });
      }
    });
  };

  return (
    <>
      <div>
        {usuarioCadastrado ? (
          <p>Bem-vindo, {nome}!</p>
        ) : (
          <div>
            <input
              type="text"
              placeholder="Nome do usurio"
              value={nome}
              onChange={(e) => setNome(e.target.value)}
            />
            <button onClick={handleCadastroUsuario}>Cadastrar Usurio</button>
          </div>
        )}
      </div>
      <div>
        {usuarioCadastrado && (
          <Link to="/about">
            <h1>Jogo</h1>
          </Link>
        )}
      </div>
    </>
  );
};

export default Home;
